
/* eslint no-unused-vars: 0 */
import ifDefined from '@ui5/webcomponents-base/dist/renderer/ifDefined.js';
import { html, svg, repeat, classMap, styleMap, unsafeHTML, setTags, setSuffix } from '@ui5/webcomponents-base/dist/renderer/LitRenderer.js';
const block0 = (context) => { return html`<div class="ui5-uc-root" role="region" aria-roledescription="${ifDefined(context._roleDescription)}" @drop="${context._ondrop}"><div class="ui5-uc-header"><slot name="header"></slot></div><div class="${classMap(context.classes.content)}"><ui5-list mode="${ifDefined(context.mode)}" @ui5-selection-change="${ifDefined(context._onSelectionChange)}" @ui5-item-delete="${ifDefined(context._onItemDelete)}"><slot></slot></ui5-list>${ context._showNoData ? block1(context) : block2(context) }</div></div>`; };
const block1 = (context) => { return html`<div class="uc-no-files"><div class="icon-container"><ui5-icon name="document"></ui5-icon></div><ui5-title level="H2" wrap>${ifDefined(context._noDataText)}</ui5-title><ui5-label class="subtitle" wrap>${ifDefined(context._noDataDescription)}</ui5-label></div>`; };
const block2 = (context) => { return html`${ context._showDndOverlay ? block3(context) : undefined }`; };
