
/* eslint no-unused-vars: 0 */
import ifDefined from '@ui5/webcomponents-base/dist/renderer/ifDefined.js';
import { html, svg, repeat, classMap, styleMap, unsafeHTML, setTags, setSuffix } from '@ui5/webcomponents-base/dist/renderer/LitRenderer.js';
const block0 = (context) => { return html`<ui5-responsive-popover horizontal-align="Center" placement-type="Bottom" class="${classMap(context.classes.popover)}" @ui5-after-close=${ifDefined(context._afterClosePopover)} content-only-on-desktop prevent-focus-restore with-padding _hide-header><ul class="ui5-wizard-responsive-popover-list">${ repeat(context._groupedTabs, (item, index) => item._id || index, (item, index) => block1(item, index, context)) }</ul><div slot="footer" class="ui5-responsive-popover-footer"><ui5-button design="Transparent" @click="${context._closeRespPopover}">Cancel</ui5-button></div></ui5-responsive-popover>`; };
const block1 = (item, index, context) => { return html`<li><ui5-button icon="${ifDefined(item.icon)}" ?disabled="${item.disabled}" design="Transparent" data-ui5-header-tab-ref-id="${ifDefined(item.ariaPosinset)}" @click="${context._onOverflowStepButtonClick}">${ifDefined(item.heading)}</ui5-button></li>`; };


const main = (context, tags, suffix) => {
	setTags(tags);
	setSuffix(suffix);
	return block0(context);
};
 
export default main;