const block0 = (context) => { return html`<ui5-popover class="ui5-shellbar-menu-popover" placement-type="Bottom" @ui5-before-open=${ifDefined(context._menuPopoverBeforeOpen)} @ui5-after-close=${ifDefined(context._menuPopoverAfterClose)}><ui5-list separators="None" mode="SingleSelect" @ui5-item-press=${ifDefined(context._menuItemPress)}>${ repeat(context._menuPopoverItems, (item, index) => item._id || index, (item, index) => block1(item, index, context)) }</ui5-list></ui5-popover><ui5-popover class="ui5-shellbar-overflow-popover" placement-type="Bottom" horizontal-align="${ifDefined(context.popoverHorizontalAlign)}" no-arrow @ui5-before-open=${ifDefined(context._overflowPopoverBeforeOpen)} @ui5-after-close=${ifDefined(context._overflowPopoverAfterClose)}><ui5-list separators="None" @ui5-item-press="${ifDefined(context._actionList.itemPress)}">${ repeat(context._hiddenIcons, (item, index) => item._id || index, (item, index) => block2(item, index, context)) }</ui5-list></ui5-popover>`; };
