
/* eslint no-unused-vars: 0 */
import ifDefined from '@ui5/webcomponents-base/dist/renderer/ifDefined.js';
import { html, svg, repeat, classMap, styleMap, unsafeHTML, setTags, setSuffix } from '@ui5/webcomponents-base/dist/renderer/LitRenderer.js';
const block0 = (context) => { return html`<ui5-popover class="ui5-notification-overflow-popover" placement-type="Bottom" horizontal-align="Right" no-arrow><div class="ui5-notification-overflow-list">${ repeat(context.overflowActions, (item, index) => item._id || index, (item, index) => block1(item, index, context)) }</div></ui5-popover>`; };
const block1 = (item, index, context) => { return html`<ui5-button icon="${ifDefined(item.icon)}" design="Transparent" @click="${item.press}" ?disabled="${item.disabled}" design="${ifDefined(item.design)}" data-ui5-external-action-item-id="${ifDefined(item.refItemid)}">${ifDefined(item.text)}</ui5-button>`; };


const main = (context, tags, suffix) => {
	setTags(tags);
	setSuffix(suffix);
	return block0(context);
};
 
export default main;