
/* eslint no-unused-vars: 0 */
import ifDefined from '@ui5/webcomponents-base/dist/renderer/ifDefined.js';
import { html, svg, repeat, classMap, styleMap, unsafeHTML, setTags, setSuffix } from '@ui5/webcomponents-base/dist/renderer/LitRenderer.js';
const block0 = (context) => { return html`<div class="ui5-timeline-root" @focusin=${context._onfocusin}><ul class="ui5-timeline-list" role="listbox" aria-live="polite" aria-label="${ifDefined(context.ariaLabel)}">${ repeat(context.items, (item, index) => item._id || index, (item, index) => block1(item, index, context)) }</ul></div>`; };
const block1 = (item, index, context) => { return html`<li class="ui5-timeline-list-item" style="list-style-type: none;"><slot name="${ifDefined(item._individualSlot)}"></slot></li>`; };


const main = (context, tags, suffix) => {
	setTags(tags);
	setSuffix(suffix);
	return block0(context);
};
 
export default main;